.planbox {
	width: 100%;
	display: flex;
	justify-content: space-around;
	/* background-color: aquamarine; */
	margin: 10px 0;
}
.planbox_box {
	margin: 10px 0;
	display: flex;
	flex-direction: column;
	/* align-items: center; */
	justify-content: space-around;
	padding: 7px;
	width: 110px;
	height: 150px;
	/* border: 2px medium #664de5; */
	box-shadow: 0px 0px 0px 2px #664de5;

	border-radius: 5px;
	/* padding: 3px; */
}
.planbox_box .titleName {
	font-size: 14px;
	color: rgb(41, 38, 38);
	font-weight: 600;
}
.planbox_box .plan_details {
	font-size: 13px;
	color: grey;
	font-weight: 600;
}
