* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: Arial, Helvetica, sans-serif;
}
.Circle_container {
	display: flex;
	width: 330px;
	justify-content: space-between;
	position: relative;
	margin-bottom: 10px;
}

.line,
.linecontainer {
	height: 2px;
	width: 100%;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	z-index: -1;
}
.linecontainer {
	/* z-index: -1; */
	background-color: rgb(189, 181, 181);
}
.line {
	width: 25%;
	background-color: indigo;
}
.circle {
	width: 40px;
	height: 40px;
	border-radius: 50%;
	color: #fff;
	background-color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
	color: green;
	border: 2px solid gray;
	z-index: 10000;
}
.active {
	color: #fff;
	background-color: #664de5;
}
.details {
	display: flex;
	flex-direction: column;
}
.details input {
	border-radius: 5px;
	outline: none;
	border: 1px solid rgb(202, 197, 197);
	padding: 5px;
	width: 330px;
	height: 35px;
	font-weight: 600;
}
button {
	cursor: pointer;
}
.noborder {
	border: 0;
}
.container {
	height: 60vh;
	width: 330px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
}
.info {
	height: fit-content;
	/* background-color: #664de5; */
	width: 330px;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.info h2 {
	text-align: center;
}
.info h4 {
	color: gray;
	margin-top: 10px;
	text-align: center;
}
.details label {
	color: rgb(126, 121, 121);

	font-weight: 600;
	margin-bottom: 10px;
}
.pageContainer {
	width: 100%;
}
button {
	margin-top: 20px;
	border: 0;
	height: 35px;
	border-radius: 5px;
	background-color: #664de5;
	color: #fff;
	width: 330px;
	font-weight: 600;
}
@media (max-width: 512px) {
	.container,
	button,
	.Circle_container {
		width: 90vw;
	}
	.details input {
		width: 90vw;
	}
}
