.detailss {
	height: 150px;
	width: 400px;
	text-align: center;
	/* background-color: aquamarine; */
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	align-items: center;
}
.detailss label {
	font-size: 33px;
	color: grey;
}
.detailss p {
	text-align: center;
	word-wrap: break-word;
	margin-top: 7px;
	width: 240px;
	font-size: 16px;
	font-weight: 500;
	letter-spacing: 1px;
	color: grey;
}
